@import '../../../base';

.checkout-container {
  %value {
    font-weight: $font-weight-bold;
    font-size: $font-size-tiny;

    color: $color-input-text;
  }

  .detail-left-container {
    margin-bottom: 20px;
    margin-right: 0;

    @media screen and (max-width: 991px) {
      padding: 0 15px;
    }
  }

  .detail-right-container {
    margin-left: 40px;
    background: white;
    /* Mild shadow */
    box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.05);

    @media screen and (max-width: 991px) {
      margin-left: 0;
    }
  }

  .summary-seperator {
    border-top: 1px solid $color-input-outline;
    margin: 20px 0;

    &:nth-of-type(1) {
      margin-top: 0;
    }
  }

  .card-body {
    padding: 0 !important;
  }

  .company-product-container {
    margin-bottom: -1px;
    border: none;
    border-bottom: solid 1px $color-line;
  }

  .form-header {
    text-align: center;
    font-size: $font-size-big;
    font-weight: $font-weight-heavy;
    color: $color-title;

    @media screen and (max-width: 690px) {
      font-size: $font-size-regular;
    }
  }

  .summary-container {
    padding: 20px;
    padding-bottom: 50px;
  }

  .charged-header {
    text-align: center;
    font-size: $font-size-regular;
    font-weight: $font-weight-heavy;
    color: $color-title;

    @media screen and (max-width: 690px) {
      font-size: $font-size-small;
    }
  }

  .piggy {
    margin-left: 40px;
    @media screen and (max-width: 991px) {
      margin-left: 0;
    }
  }
}
