@import '../../../base';

.promo-container-v3 {
  border-radius: 30px !important;
  position: relative;

  .promo-button {
    margin: 0 5px !important;

    button {
      border: 1px solid $color-input-form-landing-desc2;

      p {
        font-weight: $font-weight-bold;
        font-size: $font-size-tiny;
        text-align: center;
        color: $color-input-form-landing-desc2;
      }
    }
  }

  .text-input {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    padding: 0 0 0 10px;

    .text {
      transform: none;
      padding-left: 5px;
      max-width: calc(100% - 98px);
    }
  }
}