@import '../../../base';
.cashback-summary-new {
  max-width: 370px;
  height: 32px;
  border-radius: 50px;
  border: solid 1px $color-line;
  padding: 5px;
  display: flex;
  align-items: center;
  img {
    height: 20px;
    width: 20px;
  }

  p {
    padding-left: 9px;
    font-weight: $font-weight-heavy;
    font-size: $font-size-tiny;

    color: $color-input-text;
    margin: 0;
  }
}