    //   margin-left: 10px;
    // }

    .insurer-logo-container {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-right: 10px;

      .company-logo-container {
        display: flex;
        justify-content: center;
        align-items: center;
  
        .company-logo {
          max-width: 60px;
          max-height: 60px;
  
          // @media screen and (max-width: 768px) {
          //   max-width: 40px;
          //   max-height: 40px;
          // }
        }
      }
  
      // @media screen and (max-width: 768px) {
      //   min-height: 80px;
      //   min-width: 70px;
      //   margin-right: 10px;
      // }
    }

    .insurer-desc-container {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
  
    //   .insurer-name {
    //     font-size: 20px;
    //     font-weight: $font-weight-bold;
    //     color: $color-title;
    //     line-height: 10px;
    //   }
  
      .insurer-desc {
        font-weight: $font-weight-bold;;
        font-size: $font-size-tiny + $font-size-extra-tiny;
        color: $color-input-text;

        @media screen and (max-width: 576px) {
          font-size: $font-size-small;
        }
      }
  
      // @media screen and (max-width: 768px) {
      //   margin-top: 10px;
      //   margin-left: 10px;

      //   .insurer-name {
      //     font-size: $font-size-regular;
      //   }
    
      //   .insurer-desc {
      //     font-size: $font-size-smallest;
      //   }
      // }
    }
  }

  .insurer-right-container {
    display: flex;
    flex: 1;
    justify-content: flex-end;

    @media screen and (max-width: 576px) {
      flex: 0 0 100%;
    }

    .data-container {
      display: flex;
      align-items: center;
      height: 100%;

      .data {
        &:nth-of-type(1) {
          margin-right: 52px;
        }
        .data-name {
          line-height: 10px;
          font-weight: $font-weight-normal;
          font-size: $font-size-extra-tiny;
          color: $color-other-desc;
        }

        .data-val {
          font-weight: $font-weight-bold;
          font-size: $font-size-tiny;

          color: $color-input-text;
        }
      }
    }

    // @media screen and (max-width: 768px) {
