@import '../../../../base';

%value {
  font-weight: $font-weight-bold;
  font-size: $font-size-tiny;

  color: $color-input-text;
}

.checkout-container {
  .summary-item-container {
    display: flex;
    justify-content: center;
    align-items: baseline;
    // flex: 1;

    .summary-left {
      flex: 1;
      display: flex;
      flex-direction: column;
    }

    .summary-item-desc {
      flex: 1;
      @extend %value;
    }

    .summary-item-value {
      @extend %value;
    }

    .summary-item-sub-desc {
      font-weight: $font-weight-normal;
      font-size: $font-size-tiny;

      color: $color-other-desc;
    }

    .final-amount {
      font-size: $font-size-big;
      font-weight: $font-weight-heavy;
    }
  }

  .promo {
    input {
      max-width: 80px;
    }

    @media screen and (max-width: 991px) {
      input {
        max-width: none;
      }
    }

    @media screen and (max-width: 575px) {
      margin-bottom: 20px;
    }

    .input-wrapper-main-new {
      margin: 0;
    }
  }

  .promo-item {
    align-items: center;
  }
}
