@import '../../../../base';

%value {
  font-weight: $font-weight-bold;
  font-size: $font-size-tiny;

  color: $color-input-text;
}

.checkout-container {
  .cashback {
    %cashback-label {
      font-size: $font-size-regular;
      font-weight: $font-weight-semibold;
      color: $color-caption;
    }

    .cashback-inner {
      display: flex;
      @media screen and (min-width: 576px) {
        transform: translateY(-6px);
      }

      .checkbox-container-v3 {
        margin-bottom: 0;
      }
    }
  
    .cashback-checkbox {
      .input-wrapper-main-new {
        min-height: 0;
        margin-top: 0;
      }
    }

    .cashback-balance {
      max-width: 112px;
      transform: translateX(20px);
      @media screen and (max-width: 575px) {
        margin-bottom: 10px;
      }
    }

    .cashback-balance-charged {
      transform: none;
    }

    .cashback-text {
      @extend %value;
    }
  }
}