@import '../../../../base';

%value {
  font-weight: $font-weight-bold;
  font-size: $font-size-tiny;

  color: $color-input-text;
}

.checkout-container {
  .checkout-detail-container {
    .detail-column {
      @media screen and (max-width: 690px) {
        padding: 10px 0;
      }
      padding: 20px 0;

      .detail-padding-right {
        margin-right: 40px;
      }

      .detail-padding-left {
        margin-left: 40px;
      }

      @media screen and (max-width: 690px) {
        .detail-padding-right {
          margin-right: 0;
        }

        .detail-padding-left {
          margin-left: 0;
        }
      }

      .detail-header {
        font-weight: $font-weight-bold;
        font-size: $font-size-tiny;

        color: $color-input-text;
      }

      .detail-group {
        @media screen and (max-width: 690px) {
          margin: 6px 0;
        }
        margin: 12px 0;
        
        .detail-desc {
          font-weight: $font-weight-normal;
          font-size: $font-size-extra-tiny;

          color: $color-other-desc;
        }

        .detail-value {
          @extend %value;
          padding-left: 10px;
        }
      }
    }
  }

  .main-details {
    border-bottom: solid 1px $color-line;
  }
}