@import '../../../../base';

.checkout-container {
  .credit-card-container {
    position: relative;

    img {
      position: absolute;
      top: 18px;
      right: 24px;
      height: 18.85px;
      width: 100px;
    }
  }

  .checkout-button-container {
    .checkout-button {
      max-height: 48px;
      min-height: 0;
      height: 48px;
      width: 101%;

      p {
        font-weight: $font-weight-semibold;
        font-size: $font-size-smallest;
      }
    }
  }

  .cashback-earned-container {
    display: flex;
    justify-content: center;
    margin-top: 10px;
  }
}
